.myBG{
    height: 100%;
    background-color: #002f33;
    width: 100%;
}

.uk{
    font-size: 12px;
    background-color: #051622;
}

.uk:hover{
    background-image: linear-gradient( 135deg, #52E5E7 10%, #130CB7 100%);
    cursor: pointer;
}

.iconbg{
   color: rgb(30, 30, 30);
   text-shadow: 1px 1px 3px white;
   font-size: 2rem;
}

.bgnav{
    background-image: linear-gradient( 135deg, #11254d 10%, #057e78 100%);
}

.img{
    background-image: url("../assets/img/bggymm.png");
    height: 270px;
    width: 430px;
    background-size: cover;
    float: right;
    margin-right: -15rem;
    margin-top: 40px;
    position: relative;
}


.start1{
    float: left;
    background-image: linear-gradient( 135deg, #72EDF2 10%, #5151E5 100%);
    text-shadow: 1px 1px 2px rgb(250, 247, 247);
    box-shadow: 2px 2px 1px rgb(221, 0, 245);
    width: 128px;
    height: 23px;
    margin-left: -40px;
    font-size: 13px;
    font-weight: 100px;
    display: block;
    color: #000;
    font-family: 'Edu VIC WA NT Beginner', cursive;
    animation: neww 8s ease infinite;
    -webkit-animation: neww 8s ease infinite;
}

.startn{
    float: left;
    background-image: linear-gradient( 150deg, #72EDF2 10%, #5151E5 100%);
    text-shadow: 1px 1px 2px rgb(254, 251, 251);
    box-shadow: 2px 2px 1px rgb(176, 3, 251);
    width: 128px;
    height: 23px;
    font-size: 13px;
    font-weight: 100px;
    margin-left: 600px;
    color: #000;
    font-family: 'Edu VIC WA NT Beginner', cursive;
    animation: neww 8s ease infinite;
    -webkit-animation: neww 8s ease infinite;
}

.openingintro{
    margin-left: -60px;
    background-color: #131370;
    margin-top: 300px;
    width: 500px;
    height: 140px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: 'DM Serif Text', serif;
    font-family: 'Edu VIC WA NT Beginner', cursive;
    font-family: 'Lobster', cursive;
    font-family: 'PT Serif', serif;
}

.openingintro h1{
    margin-left: 10px;
}

.openingintro h6{
    margin-left: 10px;
}

.bgintroo{
    background-color: #002f33;
    width: 100%;
    height: 360px;
}

.textintroo{
    float: left;
    margin-top: 70px;
    justify-content: left;
    margin-left: -100px;
    font-size: 35px;
    font-family: 'DM Serif Text', serif;
    font-family: 'Edu VIC WA NT Beginner', cursive;
    font-family: 'Lobster', cursive;
    font-family: 'PT Serif', serif;
    text-shadow: 1px 2px 3px #1BA096;
}

.p2{
    font-size: 45px;
}

.p3{
    margin-top: -20px;
    font-size: 40px;
}

.svg1{
    width: 300px;
    height: 280px;
    background-image: linear-gradient( 135deg, #018b01 20%, #3a9a1f 80%);    float: right;
    margin-top: 60px;
    margin-left: -100px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

@media Screen and (max-width: 600px) {
    .myBG{
        height: 100%;
        background-color: #051622;
        width: 100%;
    }

    .bgintroo{
        height: 510px;
    }
    .textintroo {
      margin-top: -425px;
      justify-content: center;
      margin-left: 30px;
      font-size: 20px;
    }
    .start1{
        margin-left: 10px;
        margin-top: -200px;
    }
    .p2{
        font-size: 16px;
    }
    .p3{
        font-size: 20px;
    }
    .img{
        margin-top: -280px;
        position: relative;
        justify-content: center;
        display: flex;
        align-items: center;
        float: center;
        position: relative;
        margin-right: -1rem;;
    }
    .startn{
        margin-left: 160px;
        margin-top: -100px;
        justify-content: center;
    }
    .svg1{
        width: 260px;
        height: 260px;
        float: right;
        margin-top: 230px;
        margin-left: -250px;
        margin-right: 2rem;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
    }    
    .openingintro{
        display: none;
    }
  }