.avatar3{
    background-image: linear-gradient( 135deg, #CE9FFC 10%, #7367F0 100%);
    width: 250px;
    height: 22px;
    float: center;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: 59px;
    margin-left: -10px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    animation: neww 8s ease infinite;
    -webkit-animation: neww 8s ease infinite;
}

.textbenefit{
    margin-top: 100px;
}

.footer3{
    height: 2px;
    width: 114%;
    margin-left: -78px;
    margin-top: 350px;
}

@media Screen and (max-width: 600px){
    .footer3{
        height: 2px;
        width: 114%;
        margin-left: -10px;
        margin-top: 350px;
    }

    .avatar3{
        background-image: linear-gradient( 135deg, #CE9FFC 10%, #7367F0 100%);
        width: 170px;
        height: 22px;
        float: center;
        justify-content: center;
        align-items: center;
        position: absolute;
        margin-top: 18px;
        margin-left: 35px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        animation: neww 8s ease infinite;
        -webkit-animation: neww 8s ease infinite;
    }
    .textbenefit{
        text-align: center;
        margin-top: 80px;
        font-size: 16px;
    }
}